import React from 'react';
import { Route, Switch } from 'react-router-dom';

function Home() {
  return <div>Home</div>
}

function About() {
  return <div>About</div>
}

export const MainRoutes = () => (
  <Switch>
    <Route exact path='/statistics' component={About} />
    <Route exact path='/result' component={About} />
    <Route exact path='/match' component={About} />
    <Route exact path='/players' component={About} />
    <Route exact path='/' component={Home} />
  </Switch>
);